:host {
  display: inline-flex;
}

:host .star-rating {
  display: inline-flex;
  align-items: center;
  flex-direction: row;
  gap: var(--mdx-comp-star-rating-container-spacing-gap);

  &__sizing-small {
    height: var(--mdx-comp-star-rating-icon-sizing-small);
  }

  &__sizing-large {
    height: var(--mdx-comp-star-rating-icon-sizing-large);
  }

  &__star-colored {
    color: var(--mdx-comp-star-rating-stars-selected-color-default);
  }

  &__trailing {
    display: inline-flex;
    align-items: center;
    vertical-align: middle;
    gap: var(--mdx-comp-star-rating-trailing-spacing-gap-numbers);

    &__average-rating {
      color: var(--mdx-comp-star-rating-trailing-average-rating-color-default);
      font: var(--mdx-comp-star-rating-trailing-average-rating-font-default);
      font-family: var(--mdx-comp-star-rating-trailing-average-rating-font-default-font-family);
      line-height: var(--mdx-comp-star-rating-trailing-average-rating-font-default-line-height);
      font-size: var(--mdx-comp-star-rating-trailing-average-rating-font-default-font-size);
      letter-spacing: var(--mdx-comp-star-rating-trailing-average-rating-font-default-letter-spacing);
      text-transform: var(--mdx-comp-star-rating-trailing-average-rating-font-default-text-case);
      font-weight: var(--mdx-comp-star-rating-trailing-average-rating-font-weight-default);
      text-decoration: var(--mdx-comp-star-rating-trailing-average-rating-text-decoration-default);
      margin: 0;
    }
    &__average-rating:hover {
      color: var(--mdx-comp-star-rating-trailing-average-rating-color-hover);
    }

    &__counter-bracket {
      color: var(--mdx-comp-star-rating-trailing-counter-bracket-color-default);
      font: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default);
      font-family: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-font-family);
      font-weight: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-font-weight);
      line-height: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-line-height);
      font-size: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-font-size);
      letter-spacing: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-letter-spacing);
      text-transform: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-text-case);
      text-decoration: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-text-decoration);
      margin: 0;
    }
    &__counter-bracket:hover {
      color: var(--mdx-comp-star-rating-trailing-counter-bracket-color-hover);
    }
  }

  &__leading {
    display: inline-flex;
    align-items: center;
    color: var(--mdx-comp-star-rating-leading-color-default);
    font: var(--mdx-comp-star-rating-leading-font-default);
    font-family: var(--mdx-comp-star-rating-leading-font-default-font-family);
    line-height: var(--mdx-comp-star-rating-leading-font-default-line-height);
    font-size: var(--mdx-comp-star-rating-leading-font-default-font-size);
    letter-spacing: var(--mdx-comp-star-rating-leading-font-default-letter-spacing);
    text-transform: var(--mdx-comp-star-rating-leading-font-default-text-case);
    text-decoration: var(--mdx-comp-star-rating-leading-font-default-text-decoration);
    font-weight: var(--mdx-comp-star-rating-leading-font-weight-default);
    margin: 0;
  }
  &__leading:hover {
    color: var(--mdx-comp-star-rating-leading-color-hover);
  }
}

