import { Component, h, Prop } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';
import { SINGLE_STAR_RATING_SIZES, SINGLE_STAR_RATING_VARIANTS } from './export';

@Component({
  tag: 'mdx-single-star-rating',
  styleUrl: 'single-star-rating.scss',
  shadow: true,
})
export class SingleStarRating {
  @Prop()
  variant: (typeof SINGLE_STAR_RATING_VARIANTS)[keyof typeof SINGLE_STAR_RATING_VARIANTS] =
    SINGLE_STAR_RATING_VARIANTS.RATING;

  @Prop()
  size: (typeof SINGLE_STAR_RATING_SIZES)[keyof typeof SINGLE_STAR_RATING_SIZES] = SINGLE_STAR_RATING_SIZES.SM;

  /**
   * Number from 0 to 5
   */
  @Prop()
  value = 0;

  @Prop()
  ratingCount = 0;

  @Prop()
  // eslint-disable-next-line @stencil-community/reserved-member-names
  ariaDescription?: string;

  @Prop()
  ratingLink?: string;

  @Prop()
  ratingLinkTitle?: string = 'go to rating detail';

  @Prop()
  ratingLinkTarget: '_blank' | '_self' | '_parent' | '_top' = '_self';

  render() {
    const starSize: (typeof ICON_SIZES)[keyof typeof ICON_SIZES] =
      this.size === SINGLE_STAR_RATING_SIZES.LG ? ICON_SIZES.SM : ICON_SIZES.XS;
    return (
      <div
        class={`star-rating ${this.size === SINGLE_STAR_RATING_SIZES.LG ? 'star-rating__sizing-large' : 'star-rating__sizing-small'}`}
        aria-description={this.ariaDescription || `star rating; ${this.value} of 5 stars; ${this.ratingCount} ratings`}
      >
        {this.variant === SINGLE_STAR_RATING_VARIANTS.COUNTER && <div class="star-rating__leading">{this.ratingCount}</div>}
        <mdx-icon-filled-star class="star-rating__star-colored" size={starSize} />
        {this.variant === SINGLE_STAR_RATING_VARIANTS.RATING && (
          <div class="star-rating__trailing">
            <a title={this.ratingLinkTitle} href={this.ratingLink} target={this.ratingLinkTarget}>
              <p class="star-rating__trailing__average-rating">{this.value}</p>
            </a>
            <p class="star-rating__trailing__counter-bracket">({this.ratingCount})</p>
          </div>
        )}
      </div>
    );
  }
}
